$color-controls: #fff5e0;
$color-nuclet-edit: #f0e0c0;
$color-grey-light: #999;
$color-grey-lighter: #ccc;
$color-button: #006688;
$color-text: #555;

.az-hidden {
  display: none;
}

// Atom Builder Viewer Block
.block-atomizer-viewer {
  display: inline-flex;
  width: calc(100% - 240px);
}

// Atom Builder Controls form
.block-atomizer-controls {
  padding: 8px;
  vertical-align: top;
  display: inline-flex;
  width: 200px;
  background: $color-controls;
  form {
    width: 100%;
  }
  label {
    font-size: 14px;
  }
  .form-checkboxes {
    margin: 0;
    .form-item {
      margin: 0;
    }
  }
  .subtitle {
    font-weight: bold;
    font-size: 13px;
    text-align: center;
  }
  .form-item {
    select {
      width: 100%;
      background: white;
    }
  }
  legend {
    color: $color-text;
  }
  #hidden-controls {
    .control-block {
      display: none;
    }
  }
  #hidden-nuclet {
    padding: 5px;
    background: $color-nuclet-edit;
    .form-item {
      margin: 0;
    }
    .az-name {
      font-size: 14px;
      font-weight: normal;
    }
    .az-slider {
      width: 43%;
    }
    .az-value {
      background: inherit;
    }
    label {
      margin: 0;
    }
  }
  #nucleus--nuclets {
    .nuclet-list {
      padding: 2px 0 0;
      font-weight: bold;
      padding: 0 5px;
      &.nuclet-selected {
        padding: -5px;
        background: $color-nuclet-edit;
        margin-top: 3px;
        .nuclet-list-button {
          background: $color-grey-light;
          color: $color-grey-lighter;
        }
      }
    }
    .nuclet-list-label {
      font-size: 12px;
    }
    .nuclet-list-button {
      padding: 1px 5px 0;
      margin-left: 10px;
      font-weight: normal;
      font-size: 10px;
      border-radius: 4px;
      border: 0;
      background: $color-button;
      color: white;
    }
    input:after {
      clear: both;
    }
  }
  #edit-nuclet-attachproton--wrapper {
    .fieldset-wrapper {
      display: inline-flex;
    }
    #edit-nuclet-attachproton {
      display: inline-flex;
    }
  }
  #nuclet--attachProton {
    padding: 2px 4px;
  }
  .form-item-styler {
    margin: 0 0 10px;
    select {
      font-size: 16px;
      font-weight: bold;
    }
  }
  .az-control.button,
  .button-wrapper > div,
  .link-button {
    color: white;
    background: $color_button;
    border-radius: 6px;
    padding: 2px 0;
    font-weight: bold;
    text-align: center;
    margin: 3px 10%;
    width: 80%;
  }
  .styler-controls .control-block {
    display: none;
  }
  .control-block {
    hr {
      margin: 10px 0 15px;
      border-top: 1px solid #bbb;
    }
  }

  .az-header {
    font-size: 16px;
    font-weight: bold;
    margin-bottom: 8px;
  }
  .az-label {
    font-weight: bold;
    font-size: 14px;
    margin-top: 10px;
  }
  .az-indent .az-name {
    padding-left: 15px;
  }
  .az-control-position,
  .az-control-rotation,
  .az-control-range {
    font-size: 12px;
    .az-name {
      vertical-align: middle;
      display: inline-flex;
      width: 30%;
    }
    .form-item {
      margin: 0;
    }
    .form-item-value {
      vertical-align: middle;
      display: inline-flex;
      .az-value {
        text-align: right;
        width: 2.2em;
        padding: 0;
        border: 0;
        margin: 0 5px;
        background: $color-controls;
      }
    }
    .az-slider {
      width: 46%;
      display: inline-flex;
      vertical-align: middle;
      background: inherit;
      margin: 1px 0px;
      padding: 0;
//    height: 18px;
      border: 0;
    }
    .az-max,
    .az-min {
      vertical-align: middle;
      font-size: 90%;
      width: 2em;
    }
  }
  #style--name {
    width: 100%;
  }
  .form-type-color {
    margin: 10px 0 2px;
    &not:first-child {
      border-top: solid 1px #aaa;
      padding-top: 10px;
    }
    label {
      display: inline-block;
      vertical-align: top;
      font-size: 16px;
      font-weight: bold;
      width: 60%;
      margin: 0px 0 0;
    }
    input[type="color"] {
      display: inline-block;
      width: 37%;
      height: 26px;
      padding: 0;
      border: 0;
      -webkit-box-shadow: 0 0 0 0;
    }
  }
  .form-type-checkbox {
    margin: 0 0 0 10px ;
  }
  .button {
    margin: 5px 0;
  }
  .form-type-radio {
    label {
      font-size: 13px;
    }
    .form-radio {
      margin: 0 0 0 15px;
    }
    .form-item-radio {
      margin: 0;
      .form-type-checkbox {
        margin: 0 0 0 10px;
      }
      input {
        margin: 0;
      }
    }
  }
  .js-form-submit {
    padding: 5px 8px;
    border-radius: 8px;
  }
  .form-text {
    width: 100%;
  }
  .az-min,
  .az-max,
  .az-label,
  .form-item-value,
  .form-item-range {
    display: inline;
  }
}

